*,
*::before,
*::after {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

button,
input,
select,
textarea {
  font-family: inherit;
  font-size: 100%;
}

/* Body Styles */
body {
  font-family: Helvetica, Arial, system-ui;
  font-size: 16px;
  line-height: 1.5;
}

input {
  width: 100%;
  max-width: 400px;
  padding: 0.75rem 1rem;
  font-size: 1rem;
  border: none;
  border-radius: 999px;
  box-shadow: 0 4px 10px rgba(0, 0, 0, 0.1);
  outline: none;
  background-color: #fff;
  color: #333;
  margin: 10px;
}

input::placeholder {
  color: #aaa;
}

input:focus {
  box-shadow: 0 6px 12px rgba(0, 0, 0, 0.15);
  background-color: #f9f9f9;
}

#container {
  display: flex;
  flex-direction: column;
  height: 100vh;
}

#current-location,
#clock {
  margin-left: auto;
  margin-right: 10px;
  font-size: 2rem;
  color: white;
  padding: 0.5rem 1rem;
  border-radius: 0.5rem;
  text-shadow: 0 1px 3px rgba(0, 0, 0, 0.8);
}

#week-box {
  background: rgba(255, 255, 255, 0.6);
  display: flex;
  gap: 10px;
  padding: 10px;
  margin-top: auto;
}

#today-box {
  text-align: center;
  margin: 10px;
  padding: 10px;
}

#today-mid-box {
  display: flex;
  justify-content: center;
  align-items: center;
}

#today-date {
  font-size: 2rem;
}

#today-temp {
  font-size: 4rem;
}
#today-date,
.day {
  background-color: #333;
  color: #fff;
  border-radius: 999px;
}

.day,
.day-temp {
  font-size: 2rem;
}

#today-desc {
  font-style: italic;
}

#today-mid-box img {
  width: clamp(50px, 150px, 500px);
  height: clamp(50px, 150px, 500px);
}

.day-box {
  text-align: center;
  padding: 20px;
  border-left: 1px solid grey;
}

#week-box .day-box:nth-child(8) {
  border-right: 1px solid grey;
}

.day-box img {
  width: clamp(15px, 100px, 250px);
  height: clamp(15px, 100px, 250px);
}

.summary {
  font-size: 1.2rem;
  padding: 10px;
}
